import React from "react";  


class CountingNo extends React.Component{
    state = {
        count:0,
    };
    increaeCount=()=>{
        this.setState({
            count: this.state.count+1,
        });
    };
    decreaseCount=()=>{
        if(this.state.count>0){
            this.setState({
                count: this.state.count-1,
            });
        }
    };
    reset=()=>{
        this.setState({
            count:0,
        });
    };
    render(){
        const myStyle = {
            background:this.state.count >=5 ? "dodgerblue" : ""
        }
        const myDiv ={
            background:this.state.count===10 ? "black" : "",
            color:"red"
        }
    
        return (
        <>
            <div className="header">
                <h1>Counter App</h1>
            </div>
            <div style={myDiv} className="container">
                <div>
                    <button onClick={this.decreaseCount}>-</button>
                    <span style={myStyle}>{this.state.count}</span>
                    <button onClick={this.increaeCount}>+</button>
                </div>
                <div className="reset-btn">
                    <button onClick={this.reset}>RESET</button>
                </div>
            <div>
                {
                    this.state.count>=5 ? <p>Yes!! Today is Monday...</p> : ""
                }
            </div>
            </div>
            <div className="footer">
                <p>Designed by : <b>Mohammed Abdulhakeem</b></p>
            </div>
        </>
        );
    }
}

export default CountingNo;